import { DocumentSnapshot } from 'firebase/firestore';

export interface ProductInterface {
    productId?: string;
    categoryId: string;
    content: number;
    description: string;
    image: string | null;
    name: string;
    price: number;
    promo: number;
    stocks: number;
    storageLife: number;
    type: string;
}

class ProductModel {
    product: ProductInterface;

    constructor(product: ProductInterface) {
        this.product = product;
    }

    getProduct(): ProductInterface {
        return this.product;
    }

    setImage(imageUrl: string) {
        this.product.image = imageUrl;
    }
}

export function convertProductQuerySnapshots(snapshot: DocumentSnapshot) {
    const productData = snapshot.data();
    let productQueryData: ProductInterface = {
        productId: snapshot.id,
        categoryId: productData.categoryId,
        content: productData.content,
        description: productData.description,
        image: productData.image,
        name: productData.name,
        price: productData.price,
        promo: productData.promo,
        stocks: productData.stocks,
        storageLife: productData.storageLife,
        type: productData.type
    };

    return new ProductModel(productQueryData);
}

export default ProductModel;