import React from 'react';
import Card from '@mui/material/Card';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import InputAdornment from '@mui/material/InputAdornment';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import { ProductInterface } from 'src/models/products';

interface Props {
    productInfo: ProductInterface,
    onTextChange: (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => void,
    onNumberChange: (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => void,
    onSubmitForm: (event: React.FormEvent<HTMLFormElement>) => void,
    error: string | null
}

function ProductForm({ productInfo, onTextChange, onNumberChange, onSubmitForm, error }: Props) {
    const { categoryId, content, description, name, price, promo, stocks, storageLife, type } = productInfo;

    return (
        <>
            <Card sx={{ width: '100%', padding: 3 }}>
                <form onSubmit={event => onSubmitForm(event)}>
                    <Grid container spacing={2}>
                        <Grid item sm={12} md={6} sx={{ width: '100%' }}>
                            <TextField 
                                fullWidth
                                required
                                name="name" 
                                label="Product Name" 
                                variant="outlined"
                                value={name}
                                onChange={event => onTextChange(event)}
                            />
                        </Grid>
                        <Grid item sm={12} md={6} sx={{ width: '100%' }}>
                            <TextField 
                                fullWidth
                                required
                                name="price" 
                                type="number"
                                label="Price" 
                                variant="outlined"
                                value={price}
                                onChange={event => onNumberChange(event)}
                                InputProps={{
                                    startAdornment: <InputAdornment position="start">₱</InputAdornment>
                                }}
                            />
                        </Grid>
                        <Grid item sm={12} md={6} sx={{ width: '100%' }}>
                            <TextField 
                                fullWidth
                                required
                                name="stocks" 
                                type="number"
                                label="Stocks" 
                                variant="outlined"
                                value={stocks}
                                onChange={event => onNumberChange(event)}
                            />
                        </Grid>
                        <Grid item sm={12} md={6} sx={{ width: '100%' }}>
                            <TextField 
                                fullWidth
                                required
                                name="content" 
                                type="number"
                                label="Content" 
                                variant="outlined"
                                value={content}
                                onChange={event => onNumberChange(event)}
                                InputProps={{
                                    endAdornment: <InputAdornment position="end">grams</InputAdornment>
                                }}
                            />
                        </Grid>
                        <Grid item sm={12} md={6} sx={{ width: '100%' }}>
                            <TextField 
                                fullWidth
                                required
                                name="promo" 
                                type="number"
                                label="Promo" 
                                variant="outlined"
                                value={promo}
                                onChange={event => onNumberChange(event)}
                                InputProps={{
                                    endAdornment: <InputAdornment position="end">%</InputAdornment>
                                }}
                            />
                        </Grid>
                        <Grid item sm={12} md={6} sx={{ width: '100%' }}>
                            <TextField 
                                fullWidth
                                required
                                name="storageLife" 
                                type="number"
                                label="Storage Life" 
                                variant="outlined"
                                value={storageLife}
                                onChange={event => onNumberChange(event)}
                                InputProps={{
                                    endAdornment: <InputAdornment position="end">days</InputAdornment>
                                }}
                            />
                        </Grid>
                        <Grid item sm={12} md={6} sx={{ width: '100%' }}>
                            <TextField 
                                fullWidth
                                required
                                name="categoryId" 
                                label="Category" 
                                variant="outlined"
                                value={categoryId}
                                onChange={event => onTextChange(event)}
                            />
                        </Grid>
                        <Grid item sm={12} md={6} sx={{ width: '100%' }}>
                            <TextField 
                                fullWidth
                                required
                                name="type" 
                                label="Type" 
                                variant="outlined"
                                value={type}
                                onChange={event => onTextChange(event)}
                            />
                        </Grid>
                        <Grid item sm={12} md={12} sx={{ width: '100%' }}>
                            <TextField 
                                fullWidth
                                required
                                multiline
                                rows={3}
                                name="description" 
                                label="Description" 
                                variant="outlined"
                                value={description}
                                onChange={event => onTextChange(event)}
                            />
                        </Grid>
                        {error !== null && (
                            <Grid item sm={12} md={12} sx={{ width: '100%' }}>
                                <Box component="div"
                                    sx={{
                                        display: 'flex',
                                        justifyContent: 'center',
                                        alignItems: 'center',
                                        backgroundColor: '#f294a5',
                                        height: 50,
                                        width: '100%',
                                        filter: 'brightness(85%)'
                                    }}
                                >
                                    <Typography variant="h5" align="center" color="white">Image is required!</Typography>
                                </Box>
                            </Grid>
                        )}
                        <Grid item sm={12} md={12} sx={{ width: '100%' }}>
                            <Button type="submit" variant="contained" fullWidth>SAVE PRODUCT</Button> 
                        </Grid>
                    </Grid>
                </form>
            </Card>
        </>
    )
}

export default ProductForm
