import React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

interface Props {
    openDialog: boolean,
    setOpenDialog: (value: boolean) => void,
    deleteProduct: () => void
}

function DeleteConfirm({ openDialog, setOpenDialog, deleteProduct }: Props) {
    function handleClose() {
        setOpenDialog(false);
    }

    return (
        <Dialog
            open={openDialog}
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle id="alert-dialog-title">
            {"Are you sure you want to delete this product?"}
            </DialogTitle>
            <DialogContent>
            <DialogContentText id="alert-dialog-description">
                By clicking delete, this product will no longer be available. If you need this product in the future, you may need to add it again.
            </DialogContentText>
            </DialogContent>
            <DialogActions>
            <Button onClick={handleClose}>Cancel</Button>
            <Button onClick={deleteProduct} color="error" autoFocus>
                Delete
            </Button>
            </DialogActions>
        </Dialog>
    )
}

export default DeleteConfirm
