import React, { useState } from 'react';
import { Helmet } from 'react-helmet-async';
import Grid from '@mui/material/Grid';
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import Stack from '@mui/material/Stack';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

import { useParams } from 'react-router-dom';
import { useFirebaseAuth } from 'src/contexts/FirebaseAuthProvider';
import { doc, getDoc, setDoc, deleteDoc } from "firebase/firestore";
import ProductModel, { ProductInterface, convertProductQuerySnapshots } from 'src/models/products';

//icons
import CloseIcon from '@mui/icons-material/Close';
import DeleteIcon from '@mui/icons-material/Delete';

//components
import UploadImage from '../create/UploadImage';
import ProductForm from '../create/ProductForm';
import DeleteConfirm from './DeleteConfirm';
import SnackbarApp, { SnackbarProps } from 'src/components/Snackbar';


function EditProduct() {
    const { id } = useParams();
    const { database } = useFirebaseAuth();
    const navigate = useNavigate();

    const [product, setProduct] = useState<ProductInterface>({
        productId: '',
        categoryId: '',
        content: 0,
        description: '',
        image: '',
        name: '',
        price: 0,
        promo: 0,
        stocks: 0,
        storageLife: 0,
        type: ''
    });
    const [imageFile, setImageFile] = useState<Blob | null>(null);
    const [errors, setErrors] = useState<string | null>(null);
    const [snackbarApp, setSnackBarApp] = useState<SnackbarProps>({
        openSnackbar: false,
        success: true,
        message: ''
    });
    const [openDeleteDialog, setOpenDeleteDialog] = useState(false);
    const [defaultImage, setDefaultImage] = useState<string>("https://res.cloudinary.com/nathanalz/image/upload/v1641871922/ButilMNL/sample_img_ve55kv.png");

    React.useEffect(() => {
        async function getProduct() {
            const productRef = doc(database, "products", id);
            const productSnap = await getDoc(productRef);

            if (productSnap.exists()) {
                const currentProduct = convertProductQuerySnapshots(productSnap);

                setProduct(state => currentProduct.getProduct());
                setDefaultImage(state => currentProduct.getProduct().image);

            } else {
                navigate('/404');
            }
        }

        getProduct();

    }, [id, database, navigate])

    function onTextChange(event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) {
        setProduct({ ...product, [event.target.name]: event.target.value });
    }

    function onNumberChange(event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) {
        setProduct({ ...product, [event.target.name]: !isNaN(parseFloat(event.target.value)) ? parseFloat(event.target.value) : '' });
    }

    function onImageChange(event: React.ChangeEvent<HTMLInputElement>) {
        setErrors(null);
        setImageFile(event.target.files[0]);
    }

    async function uploadProductImage(): Promise<string> {
        const data = new FormData();
        data.append('file', imageFile);
        data.append('upload_preset', 'yys2sx1c');
        data.append('cloud_name', 'nathanalz');

        const response = await axios.post('https://api.cloudinary.com/v1_1/nathanalz/image/upload', data);
        const imageUrl = response.data.url;
        
        return imageUrl as string;
    }

    async function onSubmitForm(event: React.FormEvent<HTMLFormElement>) {
        event.preventDefault();
        let imageUrl = defaultImage;

        if (imageFile !== null) {
            imageUrl = await uploadProductImage();
        } 

        const productData = new ProductModel(product);
        productData.setImage(imageUrl);

        const newProductRef = doc(database, "products", id);
        setDoc(newProductRef, productData.getProduct()).then(response => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: true, message: 'Product was edited successfully!' });
            navigate('/dashboards/products');
        }).catch(error => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: false, message: 'Failed to edit product.' });
            console.log((error as Error).message);
        });
        
    }

    async function deleteProduct() {
        deleteDoc(doc(database, "products", id)).then(response => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: true, message: 'Product deleted successfully!' });
            navigate('/dashboards/products');
        }).catch(error => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: false, message: 'Failed to delete product.' });
            console.log((error as Error).message);
        });
    }

    const matches = useMediaQuery('(min-width:600px)');

    return (
        <>
            <Helmet>
                <title>Edit Product</title>
            </Helmet>
            <Container sx={{ mb: 5 }}>
                <Box sx={{ 
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    marginTop: 5,
                    marginBottom: 3
                 }}>
                     <Typography variant="h1" color="primary">Edit Product</Typography>
                     <Stack direction="row" spacing={2}>
                        {!matches ? 
                            <IconButton onClick={event => navigate('/dashboards/products')} color="error">
                                <CloseIcon />
                            </IconButton>
                            :
                            <Button variant="contained" color="error" startIcon={<CloseIcon />} onClick={event => navigate('/dashboards/products')}>
                                Cancel
                            </Button> 
                        }
                        {!matches ? 
                            <IconButton onClick={event => setOpenDeleteDialog(true)} color="error">
                                <DeleteIcon />
                            </IconButton>
                            :
                            <Button variant="contained" color="error" startIcon={<DeleteIcon />} onClick={event => setOpenDeleteDialog(true)}>
                                Delete
                            </Button> 
                        }
                     </Stack>
                 </Box>
                <Grid container spacing={2}>
                    <Grid item xs={12} md={4}>
                        <UploadImage 
                            imageFile={imageFile} 
                            onImageChange={event => onImageChange(event)} 
                            defaultImage={defaultImage}
                        />
                    </Grid>
                    <Grid item xs={12} md={8}>
                        <ProductForm 
                            productInfo={product} 
                            onTextChange={event => onTextChange(event)} 
                            onNumberChange={event => onNumberChange(event)}
                            onSubmitForm={event => onSubmitForm(event)}
                            error={errors}
                        />
                    </Grid>
                </Grid>
            </Container>
            <SnackbarApp openSnackbar={snackbarApp.openSnackbar} success={snackbarApp.success} message={snackbarApp.message} setOpenSnackbar={value => setSnackBarApp({ ...snackbarApp, openSnackbar: value })} />
            <DeleteConfirm openDialog={openDeleteDialog} setOpenDialog={value => setOpenDeleteDialog(value)} deleteProduct={() => deleteProduct()}/>
        </>
    )
}

export default EditProduct
